<template>
    <c-modal :title="model.id>0?'Editar Producto':'Registrar nuevo producto'">
        <template v-slot:body>
            <form role="form" @submit.prevent="onSubmit" @reset="onReset">
                <ValidationObserver ref="form">
                    <div class="box-body">
                        <div class="row">
                            <ValidationProvider vid="nombre" name="nombre" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-12" :class="{'has-error':errors.length==1}">
                                    <label>Nombre</label>
                                    <input v-model="model.nombre" v-uppercase type="text" name="nombre" class="form-control" placeholder="Nombre del producto o servicio" autofocus :required="!search" autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="precio" name="precio" rules="required|numeric" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Precio</label>
                                    <div class="input-group">
                                        <div class="input-group-addon">$</div>
                                        <input v-model.number="model.precio" type="number" class="form-control" placeholder="Precio del producto o servicio"  autofocus :required="!search" autocomplete="false">
                                    </div>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="precio_urg" name="precio_urg" rules="required|numeric" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Precio de Urgencia</label>
                                    <div class="input-group">
                                        <div class="input-group-addon">$</div>
                                        <input v-model.number="model.precio_urg" type="number" class="form-control" placeholder="Precio de urgencia"  autofocus :required="!search" autocomplete="false">
                                    </div>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="precio_m1" name="precio_m1" rules="required|numeric" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Precio maquila 1</label>
                                    <div class="input-group">
                                        <div class="input-group-addon">$</div>
                                        <input v-model.number="model.precio_m1" type="number" class="form-control" placeholder="Precio maquila 1"  autofocus :required="!search" autocomplete="false">
                                    </div>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="precio_m2" name="precio_m2" rules="required|numeric" v-slot="{errors}">
                                <div class="form-group col-md-6" :class="{'has-error':errors.length==1}">
                                    <label>Precio maquila 2</label>
                                    <div class="input-group">
                                        <div class="input-group-addon">$</div>
                                        <input v-model.number="model.precio_m2" type="number" class="form-control" placeholder="Precio maquila 2"  autofocus :required="!search" autocomplete="false">
                                    </div>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                    </div>
                </ValidationObserver>
                <div class="box-footer">
                    <c-btn type="submit" color="btn btn-primary" icon="fa-check" :label="labelSubmit"/>
                    <c-btn data-dismiss="modal" @click="closeModal" type="reset" color="btn btn-danger pull-right" icon="fa-remove" :label="labelReset"/>
                </div>
            </form>
        </template>
    </c-modal>
</template>

<script>
    import api from '@/services'

    export default {
        name: "t-clientes",
        props:{
            loading:{type:Boolean,require:false,default:false},
            model:{type:Object, required: false,},
            labelSubmit: {type:String, required: false, default: 'Guardar'},
            labelReset: {type:String, required: false, default: 'Cancelar'},
            search:{type:Boolean, required:false, default:false},
        },
        methods:{
            onSubmit(){
                this.validateForm(); //validar y guardar
            },
            validateForm(){
                this.$refs.form.validate().then((success)=>{
                    if (success){
                        //this.$emit('onSubmit',this.model);
                        if (this.model.id>0){
                            this.update();
                        } else this.store();
                    }
                });
            },
            onReset(){
                this.$refs.form.reset();
            },
            //methods api
            store(){
                api.productos.save(this.model).then((r)=>{
                    if (r.status === 201){
                        this.$toast.success();
                        this.$emit('store',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status == 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            update(){
                api.productos.update(this.model).then((r)=>{
                    if (r.status === 200){
                        this.$toast.info();
                        this.$emit('update',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status === 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            closeModal(){
                window.$('#modal').modal('hide');
            }
        },
    }
</script>