<template>
    <div class="">
        <div class="row">
            <div class="col-xs-12">
                <button @click="limpiar" class="btn btn-primary pull-right" data-backdrop="static" data-toggle="modal" data-target="#modal">
                    Nuevo Producto <i class="fa fa-stack-exchange"></i>
                </button>
            </div>
        </div>
        <c-data-tables @paginate="send" @refresh="refresh" title="Listado de productos" :limite="[10,20,50,100]" :total="total" :opt="opts">
            <template v-slot:table>
                <table class="table table-bordered table-condensed">
                    <thead>
                        <tr>
                            <th class="col-md-2">#</th>
                            <th>Nombre</th>
                            <th>Precio Normal</th>
                            <th>Precio Urg</th>
                            <th>Precio M1</th>
                            <th>Precio M2</th>
                            <th></th>
                        </tr>
                        <tr>
                            <th>
                                <div class="input-group">
                                    <input @keyup.enter="send" v-model.number="values.id" type="number" class="form-control input-sm">
                                    <span class="input-group-btn">
                                        <button type="button" @click="values.id=null" class="btn btn-default btn-sm">
                                            <span class="fa fa-remove"></span>
                                        </button>
                                    </span>
                                </div>
                            </th>
                            <th>
                                <div class="input-group">
                                    <input type="text" @keyup.enter="send" v-model="values.nombre" class="form-control input-sm">
                                    <span class="input-group-btn">
                                        <button @click="values.nombre=null" class="btn btn-default btn-sm">
                                            <span class="fa fa-remove"></span>
                                        </button>
                                    </span>
                                </div>
                            </th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(m,i) in model" :key="i">
                        <td>{{m.id}}</td>
                        <td>{{m.nombre}}</td>
                        <td>{{m.precio}}</td>
                        <td>{{m.precio_urg}}</td>
                        <td>{{m.precio_m1}}</td>
                        <td>{{m.precio_m2}}</td>
                        <td>
                            <button @click="confirmarDelete(m.id,i)" class="btn btn-danger btn-xs"><span class="fa fa-trash"></span></button>
                            <button @click="editar(m,i)" class="btn btn-primary btn-xs"><span class="fa fa-pencil"></span></button>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </template>
        </c-data-tables>
        <!---nuevo producto -->
        <t-productos :model="product" @store="storeEvent" @update="updateEvent"/>
        <modal-delete :model="confirmar" @onDelete="eliminar"/>
    </div>
</template>

<script>
    import tProductos from '../templates/t-productos'
    import modalDelete from '@/components/el/c-delete'
    import api from '@/services'

    export default {
        name: "productos",
        components:{tProductos,modalDelete},
        data(){
            return {
                loading:false,
                model:[],
                edit:null,
                confirmar:{id:0,index:null},
                product: {
                    id:0,
                    nombre:null,
                    precio:null,
                    precio_urg:null,
                    precio_m1:null,
                    precio_m2:null
                },
                values: {
                    id:null,
                    nombre:null,
                    precio:null,
                    precio_urg:null,
                    precio_m1:null,
                    precio_m2:null,
                },
                total:0,
                opts:{
                    per_page:10,
                    page:1,
                }
            }
        },
        mounted(){
            this.$emit('title','Productos');
        },
        methods: {
            onSubmit(){
                this.loading = true;
            },
            storeEvent(e){
                this.model.unshift(e);
                this.total +=1;
            },
            updateEvent(m){
                this.model.splice(this.edit,1,m);
                this.edit = null;
                this.limpiar();
            },
            confirmarDelete(id,i){
                this.confirmar.id = id;
                this.confirmar.index = i;
                window.$('#modal-delete').modal({
                    backdrop:false,
                    show:true
                });
            },
            editar(m,i){
                this.setModel(m);
                this.edit = i;
                window.$('#modal').modal('show');
            },
            eliminar(obj){
                api.productos.destroy(obj.id).then((r)=>{
                    if (r.data){
                        this.$toast.warning();
                        this.model.splice(obj.index,1);
                    } else {
                        this.$toast.error('No se puedo eliminar el registro');
                    }
                }).catch(()=>{
                    this.$toast.catch();
                });
            },
            setModel(m){
                this.product.id = m.id;
                this.product.nombre = m.nombre;
                this.product.precio = m.precio;
                this.product.precio_urg = m.precio_urg;
                this.product.precio_m1 = m.precio_m1;
                this.product.precio_m2 = m.precio_m2;
            },
            limpiar(){
                this.product.id = 0;
                this.product.nombre = null;
                this.product.precio = null;
                this.product.precio_urg = null;
                this.product.precio_m1 = null;
                this.product.precio_m2 = null;
            },
            clearValues(){
                this.values.id = null;
                this.values.nombre = null;
                this.values.precio = null;
                this.values.precio_urg = null;
                this.values.precio_m1 = null;
                this.values.precio_m2 = null;
            },
            //datatables
            send(){
                api.productos.search(this.getParams()).then((r)=>{
                    if (r.status ===200){
                        this.model = r.data.data;
                        this.total = r.data.total;
                    }
                }).catch(()=>{});
            },
            refresh(){
                this.clearValues();
                this.send();
            },
            getParams(){
                return {...this.opts,params:this.values};
            }
        }
    }
</script>